.main-nav__toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 20.5px;
  z-index: 2;
  margin: 0;
  border: 0;
  outline: 0;
  background: 0 0;
  padding: 0;
  transform-origin: 50% 50%;
  cursor: pointer;
  transition: top 0.5s;
}
.main-nav__toggle::before {
  position: absolute;
  content: "";
  background-color: #2a2d46;
  width: 27px;
  height: 2.4px;
  box-shadow: 0 7px 0 0 #2a2d46, 0 14px 0 0 #2a2d46;
  transition: background-color 0.5s, box-shadow 0.5s;
}
@media (min-width: 1230px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .overlay {
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #000000;
  z-index: 2;
  pointer-events: auto;
  opacity: 0.85;
  animation-name: fadeOverlay;
  animation-duration: 0.4s;
}

.main-nav--opened .main-nav__toggle {
  position: relative;
  padding-left: 3px;
  width: 26px;
  height: 26px;
  background: 0 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  display: inline-block;
  background-color: white;
  width: 21px;
  height: 2px;
  box-shadow: none;
  transition: background-color width 0.2s;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  transition: transform 0.5s;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
  transition: transform 0.5s;
}

.page-header__nav-list {
  display: none;
  opacity: 0;
